import { OpcUaConfig } from '@transai/opcua-client';
import { BaseConnectorConfig } from '@xip-online-data/types';

export interface CallConfig {
  name: string;
  keyField?: string;
  offsetFilePrefix?: string;
  interval: number;
  limit?: number;
  type?: 'metric' | 'document';
  incrementalField?: string;
  metadata?: { [key: string]: string };
}

export interface ConnectorConfig extends BaseConnectorConfig {
  opcuaConfig: OpcUaConfig;
  opcUaCalls: Array<CallConfig>;
}

export interface BystronicGetRunPartHistoryResponse {
  // Format: 2025-10-20T10:20:20.4992538Z
  TimeStamp: string;

  // Format: 0f3ec10c-9dae-40bd-af3a-bb2f0aef29df
  JobGuid: string;
  PlanGuid: string;
  RunGuid: string;

  PartId: number;
  PartRefId: number;
  PartNumber: number;

  CutState: number;
  // Format: 2025-10-20T10:20:20.4992538Z
  CutStartTime: string;
  // Format: 2025-10-20T10:20:20.4992538Z
  CutEndTime: string;

  SortState: number;
  // Format: 0001-01-01T00:00:00Z
  SortStartTime: '0001-01-01T00:00:00Z';
  // Format: 0001-01-01T00:00:00Z
  SortEndTime: '0001-01-01T00:00:00Z';

  ActualCutTime: number;
  ActualStopTime: number;
  ActualWaitTime: number;
  StackAreaType: number;
}

export interface BystronicPartInfoResponse {
  // Format: 0f3ec10c-9dae-40bd-af3a-bb2f0aef29df
  JobGuid: string;
  PartId: number;
  PartReferenceIds: string;

  Name: string;
  Description: string;
  Quantity: number;
  OrderInfo: string;
  UserInfo1: string;
  UserInfo2: string;
  UserInfo3: string;
}
